<template>
<div class="tko-web">
    <div v-if="!playerConnected && !doodleSubmitted" class="main-menu">
        <div class="logo" />
        <div class="user-form">
            <form autocomplete="off" v-on:submit.prevent="onStart">
                <fieldset>
                    <label for="username" class="username-label">
                        {{ $t('FORM.NAME') }}
                        <span class="remaining">{{ nameLength - name.length }}</span>
                    </label>
                    <div class="inline">
                        <TextInput
                            id="username"
                            v-model="name"
                            class="username"
                            autocapitalize="off"
                            autocorrect="off"
                            autocomplete="off"
                            :maxlength="nameLength"
                            :placeholder="$t('FORM.NAME_PLACEHOLDER')"
                            :sanitizers="['username']"
                        />
                        <button
                            class="submit"
                            :class="{ connecting: isConnectingPlayer && isConnectingHost }"
                            :disabled="!canSubmit"
                        />
                    </div>
                </fieldset>
                <p v-bb="$t('WARNING.TOS', { submit: $t('FORM.NEXT') })" class="tos" role="complementary" />
            </form>
        </div>
    </div>
    <template v-else>
        <div v-if="!shirtAssembled" class="constrain">
            <component :is="screen.name" v-bind="ecastValues" v-on="screen.on ?? {}" />
        </div>
        <div v-if="artifact && shirtAssembled" class="post-game">
            <PostSubmit :artifact="artifact" />
        </div>
    </template>
    <div class="footer">
        <a
            class="bottom-logo"
            target="_blank"
            href="https://www.jackboxgames.com/?utm_source=jackboxtv&utm_medium=logo&utm_campaign=jackboxgames"
        >
            {{ $t('HOMEPAGE_LINK') }}
        </a>
    </div>
    <Modal class="modal" />
</div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'

import { CommonEntities, Utils } from '@tv/shared'

import { APIClient, WSClient } from '@jackboxgames/ecast'
import { TextInput } from '@tv/vue'
import Assembling from './Assembling.vue'
import Drawing from './Drawing.vue'
import PostSubmit from './PostSubmit.vue'

export default defineComponent({

    components: {
        Assembling,
        Drawing,
        TextInput,
        PostSubmit
    },

    bb: {
        tos: (_, content) => (
            `<a class="tosLink" href="https://jackboxgames.com/terms-of-service/" target="_blank">${content}</a>`
        )
    },

    data() {
        return {
            nameLength: 12,
            name: '',
            isConnectingHost: false,
            isConnectingPlayer: false,
            playerConnected: false,
            doodleSubmitted: false,
            shirtAssembled: false,
            api: new APIClient({
                host: Utils.serverUrl,
                scheme: 'https'
            }),
            roomCode: '',
            roomNotFound: false,
            hostClient: <WSClient | null> null,
            artifact: <CommonEntities.Artifact | undefined> undefined
        }
    },

    ecastKeys: {
        player: {
            fn: ({ id }) => `player:${id}`,
            hasDeepRefs: true
        },
        background: 'background'
    },

    computed: {
        screen() {
            if (this.playerConnected) {
                if (this.doodleSubmitted) {
                    return {
                        name: 'Assembling',
                        on: {
                            assemblySubmitted: () => this.handleShirtAssembled()
                        }
                    }
                }
                return {
                    name: 'Drawing',
                    on: {
                        drawingSubmitted: () => this.handleSubmitDoodle()
                    }
                }
            }

            return {
                name: 'Waiting',
                on: {}
            }
        },

        canSubmit() {
            return this.name.length > 0
        },

        ecastValues(): Record<string, any> | null {
            if (!this.$ecastValues) return null
            return this.$ecastValues
        }
    },

    methods: {
        // Form stuff

        async onStart() {
            await this.connectHost()
            await this.connectPlayer()
        },

        // Transition between components

        handleSubmitDoodle() {
            this.doodleSubmitted = true
        },

        handleShirtAssembled() {
            this.shirtAssembled = true
        },

        // Connect

        async connectHost() {
            if (this.isConnectingHost) return

            const createdRoom = await this.api.createRoom({
                appTag: 'awshirt-web',
                userId: (Math.random() * 100000).toString()
            })

            if (!createdRoom) {
                return this.showError('ERROR.ROOM_NOT_FOUND')
            }

            this.roomCode = createdRoom.code

            const roomInfo = await this.api.getRoom({
                code: this.roomCode
            })

            this.hostClient = new WSClient({
                debug: import.meta.env.TV_DEBUG,
                host: roomInfo.host,
                code: createdRoom.code,
                name: 'awshirt-web-host',
                role: 'host',
                token: createdRoom.token,
                userId: (Math.random() * 100000).toString()
            })

            this.hostClient.on('client/connected', (connected) => void this.setUpPlayer(connected))

            this.hostClient.on('artifact', (artifact) => {
                this.artifact = {
                    artifactId: artifact.artifactId,
                    rootId: artifact.rootId,
                    categoryId: artifact.categoryId
                }

                // We could explore this a bit more later if we want -- redirect to gallery immediately on artifact creation
                // const redirectUrl: string = this.galleryLink(artifact.rootId, artifact.artifactId, artifact.categoryId)

                // Redirect directly to Gallery link
                // window.location.assign(redirectUrl)
            })

            try {
                this.isConnectingHost = true

                await this.hostClient.connect()
            } catch (error) {
                console.error('[teeko-web]', error)
                this.onConnectionError()
            }

            this.isConnectingHost = false
        },

        async setUpPlayer(connected: WSClient.ClientConnected) {
            // Create a doodle
            const doodleOptions = {
                acl: ['rw role:player'],
                size: { height: 300, width: 300 }
            }
            await this.hostClient?.createDoodle('doodle', doodleOptions)

            // Create a player and associate the doodle
            const playerDoodle = {
                name: connected.name,
                maxLength: 45,
                kind: 'drawing',
                doodle: {
                    $ref: 'doodle'
                },
                responseKey: 'background'
            }
            await this.hostClient?.createObject(`player:${connected.id}`, playerDoodle, ['rw role:player'])

            // Make the background color entity and default to to the Royal color
            await this.hostClient?.createObject('background', { color: '#2f5f9d' }, ['rw role:player'])
        },

        async connectPlayer() {
            if (this.isConnectingHost || this.isConnectingPlayer) return

            const roomInfo = await this.api.getRoom({
                code: this.roomCode
            })

            const options: WSClient.InitOptions = {
                host: roomInfo.host,
                code: this.roomCode.toUpperCase(),
                name: this.name,
                role: 'player',
                userId: (Math.random() * 100000).toString(),
                debug: import.meta.env.TV_DEBUG
            }

            const client = new WSClient(options)

            try {
                this.isConnectingPlayer = true

                const welcome = await client.connect()

                this.$setupEcast({
                    room: roomInfo,
                    wsClient: client,
                    welcome
                })

                await this.hostClient?.lockRoom()
            } catch (error) {
                console.error('[teeko-web]', error)
                this.onConnectionError()
            }

            this.isConnectingPlayer = false
            this.playerConnected = true
        },

        // Errors

        onConnectionError() {
            this.showError('ERROR.GENERIC')
        },

        showError(subtextKey: string) {
            void this.$showModal('Error', {
                text: this.$t('ERROR.TITLE'),
                subtext: this.$t(subtextKey),
                dismissText: this.$t('ACTION.OK')
            })
        },

        //  Link
        galleryLink(rootId: string, artifactId: string, categoryId: string): string {
            const scheme = rootId.includes('test')
                ? 'http'
                : 'https'

            const domain = rootId.includes('test')
                ? 'games-test.jackbox.tv'
                : 'games.jackbox.tv'

            return `${scheme}://${domain}/artifact/${categoryId}/${artifactId}/`
        }
    }


})
</script>

