<template>
<div v-if="background" class="assembling">
    <Modal class="modal" />
    <form class="form-container" v-on:submit.prevent="onSubmit">
        <div class="instruction">
            <p v-t="'ASSEMBLING.INSTRUCTIONS'" />
        </div>
        <div class="assembly">
            <div class="shirt">
                <div class="drawing-container">
                    <img
                        class="drawing"
                        alt="drawing"
                        draggable="false"
                        :src="getImage()"
                    >
                </div>
                <Shirt :background="backgroundColor" />
                <div class="slogan-preview-container">
                    <div
                        class="slogan-preview"
                        v-text="slogan"
                    />
                </div>
            </div>
        </div>
        <label for="slogan" class="slogan-label">
            {{ $t('FORM.SLOGAN') }}
            <span class="remaining">{{ 45 - slogan.length }}</span>
        </label>
        <div class="inline">
            <TextInput
                id="slogan"
                v-model="slogan"
                class="slogan-input"
                autocapitalize="off"
                autocorrect="off"
                autocomplete="off"
                :maxlength="player.maxLength"
                :disabled="isSubmitting"
                :sanitizers="['html', 'emoji', 'input']"
                :placeholder="$t('FORM.SLOGAN_PLACEHOLDER')"
            />
            <button
                class="submit"
            />
        </div>
        <div class="disclaimer">
            <p>{{ $t('ASSEMBLING.DISCLAIMER') }}</p>
        </div>
    </form>
</div>
</template>


<script lang="ts">
import { defineComponent } from 'vue'
import { cloneDeep } from 'lodash'

import { DoodleCanvas } from '@tv/shared'
import { TextInput } from '@tv/vue'

import Shirt from './Shirt.vue'
import type { TeeKoWeb } from '../TeeKoWeb'

export default defineComponent({

    components: {
        Shirt,
        TextInput
    },

    props: {
        player: {
            type: Object as Prop<TeeKoWeb.Drawing.Entity>,
            required: true
        },
        background: {
            type: Object as Prop<TeeKoWeb.Drawing.Response>,
            required: true
        }
    },

    ecastKeys: {
        player: {
            fn: ({ id }) => `player:${id}`,
            hasDeepRefs: true
        },
        background: 'background'
    },

    emits: {
        assemblySubmitted: () => true
    },

    data() {
        return {
            drawingIndex: 0,
            isSubmitting: false,
            slogan: ''
        }
    },

    computed: {
        backgroundColor() {
            return this.background?.color
        }
    },

    methods: {
        getImage() {
            const canvas = new DoodleCanvas(
                document.createElement('canvas'),
                this.player.doodle
            )

            return canvas.renderImage()
        },

        async onSubmit() {
            if (!this.slogan.length) {
                console.error('SLOGAN IS EMPTY')
                return
            }

            this.isSubmitting = true

            const artifactBlob = this.createBlob()

            const doodleArtifact: TeeKoWeb.DoodleArtifact = {
                key: 'artifiact:0',
                appId: 'awshirt-web',
                categoryId: 'TeeKOWeb', // S3 bucket
                blob: artifactBlob
            }

            const result = await this.$showModal('Options', {
                text: this.$t('MODAL.TEXT'),
                options: [{
                    text: this.$t('ACTION.YES'),
                    value: 'yes'
                }, {
                    text: this.$t('ACTION.NO'),
                    value: 'no'
                }]
            })
            // Want to continue editng so return
            if (!result || result === 'no') {
                this.isSubmitting = false
                return
            }

            // Finished, let's make the artifact
            try {
                await this.$ecast.send('artifact/create', doodleArtifact)
            } catch (error) {
                this.isSubmitting = false
                this.$handleEcastError(error)
            }

            this.$emit('assemblySubmitted')
        },

        createBlob(): TeeKoWeb.DoodleBlob {
            const playerObj: TeeKoWeb.PlayerArtist = {
                avatar: 'kappa',
                index: 0,
                name: this.player.name
            }

            return {
                players: [playerObj],
                shirts: [
                    {
                        designer: playerObj,
                        drawing: {
                            artist: playerObj,
                            background: this.backgroundColor ? this.backgroundColor : '#2f5f9d',
                            lines: this.formatLines()
                        },
                        slogan: {
                            author: playerObj,
                            slogan: this.slogan
                        }
                    }
                ]
            }
        },

        formatLines(): TeeKoWeb.ArtifactLine[] {
            // Make deep clones to not mess with player prop values
            const formattedLines = cloneDeep(this.player.doodle.lines)
            return formattedLines.map((line) => ({
                color: line.color,
                points: cloneDeep(line.points),
                thickness: line.weight
            }))
        }
    }

})
</script>


