<template>
<div v-if="player" class="drawing">
    <div id="stage">
        <h1 class="instructions">{{ $t('DRAWING.INSTRUCTIONS') }}</h1>
        <div id="tools">
            <div id="weights">
                <button
                    class="weight"
                    aria-label="weight"
                    :disabled="isSubmitting"
                    :style="{
                        backgroundColor
                    }"
                    v-on:click="onChangeWeight"
                >
                    <div
                        class="circle"
                        :style="{
                            width: `${weight}px`,
                            height: `${weight}px`,
                            backgroundColor: color,
                            borderRadius: '50%'
                        }"
                    />
                </button>
            </div>
            <div id="colors">
                <input
                    v-for="(hex, idx) in colors"
                    :key="`color-${idx}`"
                    :aria-label="`color ${hex}`"
                    class="color"
                    :checked="hex === color"
                    :disabled="isSubmitting"
                    :data-color="hex"
                    name="colors"
                    :style="{ backgroundColor: hex }"
                    type="radio"
                    v-on:change="onChangeColor(hex)"
                >
            </div>
        </div>

        <div id="doodle">
            <Doodle
                :canvas-options="{ color, scale, weight }"
                :player="player"
                :style="{
                    backgroundColor,
                    pointerEvents: isSubmitting ? 'none' : ''
                }"
                hide-submit
                hide-undo
            />
        </div>

        <div id="backgrounds">
            <input
                v-for="(hex, idx) in backgroundColors"
                :key="`bg-${idx}`"
                :aria-label="`background color ${hex}`"
                class="tab"
                :checked="hex === backgroundColor"
                :disabled="isSubmitting"
                name="backgrounds"
                :style="{ backgroundColor: hex }"
                type="radio"
                v-on:change="onChangeBackgroundColor(hex)"
            >
        </div>

        <div id="actions">
            <button
                class="undo"
                :disabled="!canSubmit"
                v-on:click.prevent="onUndo"
            >
                <svg width="28" height="28" version="1.0" viewBox="0 0 46 36">
                    <path d="m6 7-6 6 6 7 7 7V17h25v11H7v8h39V8H13V4l-1-4-6 7z" />
                </svg>
            </button>
            <button
                class="submit"
                :disabled="!canSubmit"
                v-on:click.prevent="onSubmit"
            />
        </div>
    </div>
</div>
</template>


<script lang="ts">
import { defineComponent } from 'vue'

import { Doodle } from '@tv/vue'

import type { DoodleCanvas } from '@tv/shared'

import type { TeeKoWeb } from '../TeeKoWeb'


export default defineComponent({

    components: {
        Doodle
    },

    props: {
        player: {
            type: Object as Prop<TeeKoWeb.Drawing.Entity>,
            required: true
        }
    },

    emits: {
        drawingSubmitted: () => true
    },

    ecastKeys: {
        player: {
            fn: ({ id }) => `player:${id}`,
            hasDeepRefs: true
        }
    },

    data() {
        // The controller is the authority for valid colors so we can change
        // them when our shirt printing partners change their offerings.
        const backgroundColors = [
            '#2f5f9d', // Royal
            '#d02c41', // Red
            '#2a3a2a', // Forest
            '#0d162d', // Navy
            '#51346c', // Purple
            '#43342e', // Brown
            '#080808', // Black
            '#505253' // Grey
        ]
        const colors = [
            '#000000',
            '#f32938',
            '#ffa500',
            '#f8e655',
            '#44d582',
            '#0046ae',
            '#bc4df8',
            '#634a2c',
            '#ffffff'
        ]
        const weights = [5, 12, 18]

        return {
            backgroundColors,
            colors,
            weights,
            slogan: '',
            backgroundColor: backgroundColors[0],
            color: colors[colors.length - 1],
            isSubmitting: false,
            isUndoing: false,
            scale: { width: 2, height: 2 } as DoodleCanvas.Scale,
            weight: weights[0]
        }
    },

    computed: {
        canSubmit() {
            if (this.isSubmitting) return false
            if (this.isUndoing) return false
            if (this.player.doodle.lines.length <= 0) return false

            return true
        }
    },

    methods: {

        onChangeBackgroundColor(hex: string) {
            this.backgroundColor = hex
        },

        onChangeColor(hex: string) {
            this.color = hex
        },

        onChangeWeight() {
            const weightIndex = this.weights.findIndex((weight) => weight === this.weight)
            if (weightIndex === -1) return

            // Loop through available weights sequentially
            this.weight = this.weights[(weightIndex + 1) % this.weights.length]
        },

        async onSubmit() {
            this.isSubmitting = true

            try {
                // Update our background color in ecast!
                await this.$ecast.updateObject<TeeKoWeb.Drawing.Response>(
                    'background',
                    {
                        color: this.backgroundColor
                    }
                )

                await this.$ecast.lock(this.player.doodle.key)
            } catch (error) {
                this.isSubmitting = false

                this.$handleEcastError(error)
            }

            // Send doodle Artifact
            this.$emit('drawingSubmitted')
        },

        async onUndo() {
            this.isUndoing = true

            try {
                await this.$ecast.undoDoodle(this.player.doodle.key)
            } catch (error) {
                this.$handleEcastError(error)
            } finally {
                this.isUndoing = false
            }
        }

    }

})
</script>


